<template>
  <!-- table -->
  <div>
    <CRow>
      <CCol>
        <CCard>
          <CCardBody>
            <CRow>
              <CCol>
                <CSelect :label="$t('billing.month')" :options="option" class="projectSelect" />
              </CCol>
              <CCol>
                <CSelect :label="$t('billing.date')" :options="option1" class="projectSelect" />
              </CCol>
            </CRow>
            <CRow>
              <CCol>
                <label for="uid-ytow23uoh9">{{ $t('billing.groupBy') }}</label>
                <select class="form-control" v-model="groupBy">
                  <option value="1" selected="selected">專案</option>
                  <option value="2">服務</option>
                  <option value="3">SKU</option>
                </select>
              </CCol>
            </CRow>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CCard>
          <CCardBody>
            <!-- 專案 column-filter-->
            <CDataTable :items="project_items" :fields="project_fields" table-filter items-per-page-select :items-per-page="5" hover sorter pagination v-if="projectTable"> </CDataTable>
            <!-- 服務 -->
            <CDataTable :items="service_items" :fields="service_fields" table-filter items-per-page-select :items-per-page="5" hover sorter pagination v-if="serviceTable"> </CDataTable>
            <!-- SKU -->
            <CDataTable :items="sku_items" :fields="sku_fields" table-filter items-per-page-select :items-per-page="5" hover sorter pagination v-if="skuTable"> </CDataTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  </div>
</template>
<script>
import DailyChartBar from '../charts/DailyChartBar';

export default {
  name: 'DailyBilling',

  data() {
    return {
      groupBy: 1,
      option: ['2021/03', '2021/02', '2021/01'],
      option1: ['22', '21', '20', '19', '18', '17', '16', '15', '14', '13', '12', '11', '10', '09', '08', '07', '06', '05', '04', '03', '02', '01'],

      project_items: [
        { project: 'projectTest', projectID: 'projectTest', projectNumber: '8516359843', cost: '$ 120.00' },
        { project: 'myProject', projectID: 'myProject', projectNumber: '6261291528', cost: '$ 0.05' },
      ],
      service_items: [
        { service: 'Cloud Storage', cost: '$ 120.00' },
        { service: 'Stackdriver Logging', cost: '$ 0.05' },
        { service: 'Compute Engine', cost: '$2,870.25' },
        { service: 'Cloud Storage', cost: '$ 120.00' },
        { service: 'Stackdriver Logging', cost: '$ 0.05' },
        { service: 'Compute Engine', cost: '$2,870.25' },
        { service: 'Cloud Storage', cost: '$ 120.00' },
        { service: 'Stackdriver Logging', cost: '$ 0.05' },
        { service: 'Compute Engine', cost: '$2,870.25' },
      ],
      sku_items: [
        { sku: 'Download APAC', service: 'Cloud Storage', skuID: '1F8B-71B0-3D1B', flow: '0 gibibyte', cost: '$0.00' },
        { sku: 'E2 Instance Core running in Americas', service: 'Compute Engine', skuID: 'CF4E-A0C7-E3BF', flow: '1,344 hour', cost: '$821.00' },
        { sku: 'External IP Charge on a Standard VM', service: 'Compute Engine', skuID: 'C054-7F72-A02E', flow: '2,016 hour', cost: '$150.56' },
        { sku: 'Licensing Fee for Debian 10 Buster (CPU cost)', service: 'Compute Engine', skuID: '0C5C-D8E4-38C1', flow: '2,016 hour', cost: '$0.00' },
        { sku: 'Log Volume', service: 'Stackdriver Logging', skuID: '143F-A1B0-E0BE', flow: '0 gibibyte', cost: '$0.00' },
      ],
      projectTable: true,
      serviceTable: false,
      skuTable: false,
    };
  },
  components: {
    DailyChartBar,
  },
  computed: {
    project_fields() {
      return [
        {
          key: 'project',
          label: this.$t('billing.project'),
        },
        {
          key: 'projectID',
          label: this.$t('billing.projectID'),
        },
        {
          key: 'projectNumber',
          label: this.$t('billing.projectNumber'),
        },
        {
          key: 'cost',
          label: this.$t('billing.cost'),
        },
      ];
    },
    service_fields() {
      return [
        {
          key: 'service',
          label: '服務',
        },
        {
          key: 'cost',
          label: '費用',
        },
      ];
    },
    sku_fields() {
      return [
        {
          key: 'sku',
          label: 'SKU',
        },
        {
          key: 'service',
          label: '服務',
        },
        {
          key: 'skuID',
          label: 'SKU ID',
        },
        {
          key: 'flow',
          label: '用量',
        },
        {
          key: 'cost',
          label: '費用',
        },
      ];
    },
  },
  watch: {
    groupBy: {
      handler(val) {
        // console.log('hi', val);
        if (val == '1') {
          this.projectTable = true;
          this.serviceTable = false;
          this.skuTable = false;
        } else if (val == '2') {
          this.projectTable = false;
          this.serviceTable = true;
          this.skuTable = false;
        } else if (val == '3') {
          this.projectTable = false;
          this.serviceTable = false;
          this.skuTable = true;
        }
      },
    },
  },
  methods: {
    toggleDetails(item) {
      this.$set(this.items[item.id], '_toggled', !item._toggled);
      this.collapseDuration = 300;
      this.$nextTick(() => {
        this.collapseDuration = 0;
      });
    },
  },
};
</script>
